.counters{
    padding: 100px 0px 100px;
    background-color: $clr-primary;
    position: relative;
    @include breakpoint-md{
        padding: 160px 0px 160px;
    }
    @include breakpoint-lg{
        padding: 220px 150px 219px;
    }
    &__num{
        font-size: clamp(3rem, 6vw + 1rem, 5rem);
        color:$clr-white;
        font-family: 'Poppins semi-bold';
    }
    &::before{
        content:url("./../app/assets/img/clip-img/Path-1.svg");
        position: absolute;
        bottom: -80px;
        z-index: -1;
        left: 6%;
    }
}

.our-vision{
    &__title{
        font-family: 'Poppins bold';
    }
}

.rectangle-bg{
    background-image:url("./../app/assets/img/rectangle-bg.webp");
    background-repeat: no-repeat;
    position: relative;
    @include breakpoint-lg{
        background-position:180%;
        padding-top: rem(80);
    }
    &::before{
        content:url("./../app/assets/img/clip-img/Path-3.svg");
        position: absolute;
        right: 0;
        top: -50px;
        z-index: -1;
    }
}

.blob-2{
    position: absolute;
    bottom: 190px;
    left: 50px;
    z-index: -1;
}

.rectangle-bg--left{
    background-image:url("./../app/assets/img/rectangle-bg.webp");
    background-repeat: no-repeat;
    padding: 80px 0px 80px 0px;
    @include breakpoint-lg{
        background-position:-80%;
        padding: 80px 0px 200px 0px;
        margin-top: rem(140);
    }
}

.brands{
    position: relative;
    &__container{
        padding-block: em(50);
        background-color: $clr-athens-gray;

        @include breakpoint-sm{
            padding-block: em(100);
        }
        @include breakpoint-md{
            padding-block: em(150);
        }
    }
    &__logo-2{
        width: rem(258);
    }
    &__logo-3{
        width: rem(225);
    }
}

.sustainable{
    position: relative;
    &__figure{
        position: relative;
        img{
            height: rem(360);
        }
        &::after{
            content: "";
            position: absolute;
            inset:0;
            width: 100%;
            height: 100%;
            background-image: url("./../app/assets/img/canterbury-bg.svg");
            @include background-cover;
        }
    }
    &__figcaption{
        position: absolute;
        bottom: 0;
        top: 270px;
        display: inline-flex;
        align-items: center;
        justify-content: space-between;
        padding-inline: 20px;
        left: 0;
        width: 100%;
        z-index: 1;
        @include breakpoint-md{
            padding-inline: 40px;
        }
    }
}

.testimonial{
    &__thumbnail{
        // position: relative;
        overflow: hidden;
        // z-index: 1;
        &::before{
            content: url("./../app/assets/img/clip-img/Path-3.svg");
            position: absolute;
            z-index: -1;
            // right: 500px;
            // top: 90px;
            margin-top: rem(-35);
            margin-left: rem(105);
            }
    }
}

.awards{
    background-color: $clr-athens-gray;
    position: relative;
    overflow: hidden;
    z-index: 1;
    &::before{
        content:url("./../app/assets/img/clip-img/Path-9.svg");
        position: absolute;
        top: 44%;
        left: 56%;
        transform: translate(-50%,-50%);
        z-index: -1;
    }
}

.add-value{
    background-color: $clr-secondary;
    padding-block: em(128);
    position: relative;
    overflow: hidden;
    @include breakpoint-sm{
        padding-block: rem(160);
    }
    @include breakpoint-md{
        padding-block: rem(192);
    }
    @include breakpoint-lg{
        padding-block: rem(240);
    }
    @include breakpoint-xl{
        padding-block: rem(290);
    }
    &::before, &::after{
        position: absolute;
        opacity: 0.19;
    }
    &::before{
        content:url("./../app/assets/img/clip-img/Path-6.svg");
        left: rem(-230);
        top: rem(145);
        bottom: rem(189);
    }
    &::after{
        content:url("./../app/assets/img/clip-img/Path-7.svg");
        right: 0;
        bottom: rem(40);
        right: rem(-160);
    }
}

.arrow-forward-wrapper{
    display: inherit;
    svg{
        transition:all 0.4s ease-in;
        width:rem(25);
        height: rem(25);
        @include breakpoint-sm{
            width:rem(28);
            height: rem(28);
        }
        @include breakpoint-md{
            width:rem(31);
            height: rem(31);
        }
        @include breakpoint-lg{
            width:rem(34);
            height: rem(34);
        }
        &:hover{
            cursor: pointer;
            transform:translateX(6px);
            path{
                transition:all 0.4s ease-in;
                fill:$clr-secondary;
            }
        }
    }
}
